* {
    box-sizing: border-box;
}

html {
    font-family: "montserrat", sans-serif;
    line-height: 1.6;  
}
body {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: caveat, serif;
    margin: 0 ;
  
}

p {
    margin: 0 0 0em;
}
button {
    cursor: pointer;
    padding: 0;

    &:focus {
        outline: none;
    }
}




